




























































import {Component, Mixins, Vue} from 'vue-property-decorator';
import {tablePageMixin} from '@/mixins/tablePageMixin';
import {getOrgTree, delObj} from '@/api/manage/sys/organizationApi';
import OrganizationForm from '@/views/manage/sys/organization/OrganizationForm.vue';
import _array from 'lodash';

@Component({
  components: {
    OrganizationForm
  }
})
export default class OrganizationPage extends Mixins(tablePageMixin) {
  getPage = getOrgTree;
  delObj = delObj;
  pageTitle = '组织机构';
  rowKey = 'id';
  parentId: number = 0;
  parentName: string = '';

  mounted() {
    this.formPage = this.$refs.formPage;
    this.parentId = this.orgInfo.id;
    this.parentName = this.orgInfo.name;
  }

  loadData() {
    this.loading = true;
    getOrgTree(this.userInfo.organizationId).then((res: any) => {
      this.dataSource = res.data;
    }).finally(() => {
      this.loading = false;
    });
  }

  editOrg(record: any, title: any) {
    let parentObj: any = _array.find(this.dataSource, {id: record.parentId});
    console.log(parentObj);
    if (parentObj != undefined) {
      this.parentId = parentObj.id;
      this.parentName = parentObj.name;
    }
    console.log(this.parentName);
    this.handleEdit(record, title);
  }

  addOrg(record: any) {
    if (record != null) {
      this.parentId = record.id;
      this.parentName = record.name;
    }
    this.handleAdd();
  }


  classObject(record: any) {
    let clsName = '';
    switch (record.orgType) {
      case 'M':
        clsName = 'label-light-primary';
        break;
      case 'C':
        clsName = 'label-light-info';
        break;
      case 'S':
        clsName = 'label-light-danger';
        break;
    }
    return clsName;
  }

  orgTypeTxt(record: any) {
    let typeName = '';
    switch (record.orgType) {
      case 'M':
        typeName = '机构';
        break;
      case 'C':
        typeName = '公司';
        break;
      case 'S':
        typeName = '部门';
        break;
    }
    return typeName;
  }
}
