







































import {Component, Mixins, Prop} from 'vue-property-decorator';
import {formPageMixin} from '@/mixins/formPageMixin';
import {addObj, putObj} from '@/api/manage/sys/organizationApi';
import _array from 'lodash';
import {Form} from 'element-ui';

@Component({
  components: {}
})
export default class OrganizationForm extends Mixins(formPageMixin) {
  @Prop() title!: string;
  @Prop() parentId!: number;
  @Prop() parentName!: string;

  reqFunctions = {
    create: addObj,
    update: putObj
  };
  optionsStore: any = [];

  createdFormCallback() {
    this.formObj = {
      parentId: this.parentId,
      parentName: this.parentName
    };
  }

  echoDataProcess(record: any) {
    record.parentId = this.parentId;
    record.parentName = this.parentName;
  }


  formRules = {
    name: [
      {required: true, message: '请输入名称', trigger: 'blur'}
    ],
    orgType: [
      {required: true, message: '请选择组织类型', trigger: 'blur'}
    ]
  };

}
